import Vue from 'vue';
import App from './App.vue';
// import '@/assets/font-icon/iconfont'
import '@/assets/font-icon/iconfont.css'
import router from './router'

import {
    Dialog,
    Menu,
    MenuItem,
    Input,
    Switch,
    Option,
    Button,
    Select,
    Table,
    TableColumn,
    Breadcrumb,
    BreadcrumbItem,
    Form,
    FormItem,
    Tabs,
    TabPane,
    Icon,
    Row,
    Col,
    Card,
    Steps,
    Step,
    Loading,
    Message,
    MessageBox,
    InfiniteScroll,
} from 'element-ui';

Vue.use(Dialog);
Vue.use(Menu);
Vue.use(MenuItem);
Vue.use(Input);
Vue.use(Switch);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Breadcrumb);
Vue.use(BreadcrumbItem);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Icon);
Vue.use(Row);
Vue.use(Col);
Vue.use(Card);
Vue.use(Steps);
Vue.use(Step);
Vue.use(InfiniteScroll);
Vue.use(Loading);

Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$prompt = MessageBox.prompt;
Vue.prototype.$notify = Notification;
Vue.prototype.$message = Message;

import layer, {jquery} from '@/assets/layer/layer'
import '@/assets/layer/theme/default/layer.css'

import store from "@/store";

Vue.prototype.layer = layer;
Vue.prototype.$ = jquery;

Vue.config.productionTip = false;

import axios from 'axios'

axios.defaults.baseURL = '/';
// axios.defaults.baseURL = 'http://192.168.1.150:8080';
axios.defaults.withCredentials = true;

Vue.prototype.$http = axios;

import 'highlight.js/styles/github.css'

import contentmenu from 'v-contextmenu'
import 'v-contextmenu/dist/index.css'

Vue.use(contentmenu);

import APlayer from '@moefe/vue-aplayer';

Vue.use(APlayer, {
    defaultCover: 'https://github.com/u3u.png',
    productionTip: true,
});

new Vue({
    render: h => h(App),
    router,
    store,
    beforeCreate: function () {
        // 如果已经初始化, 则跳转后文件首页, 否则跳转后初始化页
        this.$http.get('is-installed').then((response) => {
            let data = response.data;
            if (data.code !== 0) {
                if (!this.$route.fullPath.includes("/main")
                    && !this.$route.fullPath.includes("/admin")
                    && !this.$route.fullPath.includes("/login")) {
                    this.$router.push('/main');
                }
            } else {
                this.$router.push('/install')
            }
        });

        //  REQUEST 请求异常拦截
        axios.interceptors.response.use(config=> {
            return config;
        }, error=> {
            let msg = error.response.data.msg;
            if (msg === '未登录') {
                this.$router.push('/login');
            } else {
                this.$message({
                    message: msg,
                    type: 'error'
                });
            }
            return Promise.resolve(error);
        });
    }
}).$mount('#app');