<template>
    <el-row>
        <el-col :span="3">
            <el-menu
                    :default-active="this.$route.path !== '/admin' ? this.$route.path : '/admin/site'"
                    class="el-menu-vertical-demo"
                    :router="true">
                <el-menu-item index="/admin/site">
                    <i class="el-icon-setting"></i>
                    <span slot="title">站点设置</span>
                </el-menu-item>
                <el-menu-item index="/admin/storage">
                    <i class="el-icon-s-operation"></i>
                    <span slot="title">存储策略</span>
                </el-menu-item>
                <el-menu-item index="/admin/password">
                    <i class="el-icon-key"></i>
                    <span slot="title">修改密码</span>
                </el-menu-item>
            </el-menu>
            <el-button class="bottom-btn" @click="clearCache" type="primary" round>清理缓存</el-button>
        </el-col>
        <el-col :span="12">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </el-col>
    </el-row>

</template>

<script>
    export default {
        name: "Index",
        data() {
            return {
                active: '/admin/storage'
            }
        },
        methods: {
            clearCache() {
                this.$http.get('admin/clear-cache').then(() => {
                    this.$message({
                        message: '操作成功',
                        type: 'success'
                    });
                })
            }
        }
    }
</script>

<style scoped>
    .el-row {
        height: 100vh;
    }
    .el-menu {
        height: 100%;
    }
    .bottom-btn {
        bottom: 5px;
        left: 20px;
        position: absolute;
    }
</style>