<template>
    <el-row>
        <el-col :span="16">
            <el-form id="pwdForm" ref="form" :model="form" label-width="auto" :status-icon="true">
                <el-form-item label="站点名称">
                    <el-input v-model="form.siteName"></el-input>
                </el-form-item>

                <el-form-item label="右侧信息框">
                    <el-switch v-model="form.infoEnable"></el-switch>
                </el-form-item>

                <el-form-item label="搜索">
                    <el-switch v-model="form.searchEnable"></el-switch>
                </el-form-item>

                <el-form-item label="搜素区分大小写" v-if="form.searchEnable">
                    <el-switch v-model="form.searchIgnoreCase"></el-switch>
                </el-form-item>

                <el-form-item label="存储策略">
                    <el-select v-model="form.storageStrategy" placeholder="请选择存储策略">
                        <el-option label="阿里云 OSS" value="aliyun"></el-option>
                        <el-option label="腾讯云 COS" value="tencent"></el-option>
                        <el-option label="华为云 OBS" value="huawei"></el-option>
                        <el-option label="七牛云 KODO" value="qiniu"></el-option>
                        <el-option label="又拍云 USS" value="upyun"></el-option>
                        <el-option label="FTP" value="ftp"></el-option>
                        <el-option label="本地存储" value="local"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="submitForm('form')">确认</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>
    import qs from 'qs';
    export default {
        name: "SiteSetting",
        data() {
            return {
                form: {
                    siteName: '',
                    storageStrategy: '',
                    infoEnable: false,
                    searchEnable: false,
                    searchIgnoreCase: false,
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$http.post('admin/config', qs.stringify(this.form)).then((response) => {
                            this.$message({
                                message: '保存成功',
                                type: 'success'
                            });
                        })
                    } else {
                        return false;
                    }
                });
            }
        },
        mounted() {
            this.$http.get('admin/config').then((response) => {
                this.form = response.data.data;
            })
        }
    }
</script>

<style scoped>
    #pwdForm {
        margin-top: 20px;
        margin-left: 20px;
    }

    #pwdForm >>> .el-select {
        width: 100%;
    }
</style>